                    <div class="info">{{token_unit}}</div>
                </div>
                <div class="box" style="margin-top:0.4rem;">
                    <div class="tit">{{$t('m.token.contract_address')}}</div>
                    <div class="info info-link info-address share-link"><input type="text" :value="ess_address" readonly="readonly" id="share-link1"><a href="" target="_blank">{{addressValue(ess_address,8,26,'......')}} </a>&nbsp;&nbsp;<i class="iconfont" id="j-copy1">&#xe64b;</i></div>
                </div>
            </div>
            <div class="m_block clear_fix">
                <div class="box box1">
                    <div class="tit">{{$t('m.token.box1.dividend_account')}}（ETH）</div>
