                        <div class="titname">{{$t('m.token.box1.bonus_pool')}}</div>
                        <div class="num">{{(Number(total_bonus-lock_total_bonus).toFixed(decimals))|splitText1}}.<span>{{(Number(total_bonus-lock_total_bonus).toFixed(decimals))|splitText2}}</span></div>
                        <div class="tokenname">ETH</div>
                    </div>
                    <div class="flex-box czbox">
