</template>
<script>
    import { setCookie, getCookie } from '@/assets/js/cookieHandle';

    export default {
        components: {
        },
        data () {
            return {
                decimals:4, //显示的精度
                step:1,
                ess_address:essAddress,      //合约地址
                account_address:"",  //当前账户地址
                token_balance:0,     //当前账户的token余额
                token_unit:"ESS",       //单位
                lock_bonus:0,        //锁定分红
                withdraw_bonus:0,    //可提现分红
                lock_percent:"",      //锁定的分红占比
                total_bonus:0,       //分红奖池总额
                lock_total_bonus:0,   //锁定总额
                jackpot_1:0,
                jackpot_2:0,
                route_domain:route_domain
            }
        },
        created(){
            this.datacheckweb3=checkweb3();
        },
        mounted () {

            var _vue = this;

            if(!checkweb3()){

                //console.log("钱包状态异常");
                return false;
            }

            var _vue = this;
            //任务的队列，txrecipet=>funcname表示，轮询到对应的tx时，执行合约的方法
            window.eventQueue = {};
            //轮询区块信息，回调事件
            window.loopBlock = function(lastblock){
                web3.eth.getBlockNumber().then(function(newblock){
                    //只处理新块
                    if(newblock>lastblock){
                        _vue.load_account();
                        web3.eth.getBlock(newblock).then(function(blockinfo){
                            console.log(blockinfo);
                            console.log(eventQueue);
                            try{
                                for(var i=0;i<blockinfo.transactions.length;i++){
                                    var txhash = blockinfo.transactions[i];
                                    for(var queuehash in eventQueue){
                                        if(txhash==queuehash){
                                            var funcname = eventQueue[queuehash];
                                            delete eventQueue[queuehash];
                                            try{
                                                eval(funcname+"('"+txhash+"')");
                                            }catch(ex){
                                                console.log(ex);
                                                console.log(funcname+"不存在");
                                            }
                                        }
                                    }
                                }
                            }catch(ex){

                            }
                            loopBlock(newblock);
                        });
                    }
                    else
                    {
                        loopBlock(newblock);
                    }
                });
            };
            loopBlock(0);
            //end loop

            _vue.account_address = window.eth_account;
            _vue.load_account();
            console.log(Number(this.total_bonus-this.lock_total_bonus).toFixed(this.decimals));
            var button = document.getElementById("j-copy");
			button.onclick = function(event){
				document.getElementById("share-link").select();
				document.execCommand("copy",false,null);
				_vue.$message({
                    message: _vue.$t('m.token.copyed'),
                    center: true
                });
			};
            var button = document.getElementById("j-copy1");
			button.onclick = function(event){
				document.getElementById("share-link1").select();
				document.execCommand("copy",false,null);
				_vue.$message({
                    message: _vue.$t('m.token.copyed'),
                    center: true
                });
			};
        },
        methods:{
            /**
             * 切换步骤
             */
            changestep (value){
                if (value==2) {
                    this.step=2
                }else{
                    this.step=1
                }
                $('.content').scrollTop(0);
            },
            /**
             * 字符串截断
             */
            addressValue(str,start,length,replace){
                try {
                    var firstStr = str.substr(0, start);
                    var endStr = str.substr(start + length, str.length);
                    return firstStr + replace + endStr;
                }catch(ex){

                }
            },
            send_withdraw(){
                if(!checkweb3()){
                    // console.log("钱包状态异常");
                    return false;
                }
                // console.log("提现");
                var price = 0;
                var recommend_gas_price = "20";
                essContract.methods.withdraw().send({from: eth_account,value:price,gasPrice:web3.utils.toWei(recommend_gas_price,"gwei")},function(err,txreceipt){
                    if(!err){
                        eventQueue[txreceipt] = "withdraw_done";
                        window.withdraw_done = function(txhash){
                            // console.log("提现成功"+txhash);
                        };
                    }
                });
            },
            send_computebonus(){
                if(!checkweb3()){
                    // console.log("钱包状态异常");
                    return false;
                }
                // console.log("执行分红");
                var price = 0;
                var recommend_gas_price = "20";
                essContract.methods.computeBonus().send({from: eth_account,value:price,gasPrice:web3.utils.toWei(recommend_gas_price,"gwei")},function(err,txreceipt){
                    if(!err){
                        eventQueue[txreceipt] = "computebonus_done";
                        window.computebonus_done = function(txhash){
                            // console.log("分红执行成功"+txhash);
                        };
                    }
                });
            },
            //刷新token账户信息
            load_account(){
                var _vue = this;
                //获取ess余额
                essContract.methods.balanceOf(_vue.account_address).call(null,function(err,value){
                    var amount = web3.utils.fromWei(value.valueOf(),"ether");
                    amount = Number(amount).toFixed(_vue.decimals);
                    _vue.token_balance = amount;

                    //获取单位
                    essContract.methods.symbol().call(null,function(err,symbol){
                        _vue.token_unit = symbol.valueOf();
                    });

                    //获取锁定占比
                    essContract.methods.totalSupply().call(null,function(err,total){
                        if(total==0)
                            _vue.lock_percent = 0;
                        else
                        {
                            var percent = value.valueOf()/total.valueOf()*100;
                            percent = Number(percent).toFixed(_vue.decimals);
                            _vue.lock_percent = percent;
                        }

                    });


                });

                //获取锁定分红
                essContract.methods.lockedBonus(eth_account).call(null,function(err,value){
                    if(!err){
                        var amount = web3.utils.fromWei(value.valueOf(),"ether");
                        amount = Number(amount).toFixed(_vue.decimals);
                        _vue.lock_bonus = amount;
                    }

                });



                //获取可提现分红
                essContract.methods.withdrawableBonus(eth_account).call(null,function(err,value){
                    if(!err){
                        var amount = web3.utils.fromWei(value.valueOf(),"ether");
                        amount = Number(amount).toFixed(_vue.decimals);
                        _vue.withdraw_bonus = amount;
                    }
                    else
                    {
                        console.log(err);
                    }
                });

                //所有奖池
                web3.eth.getBalance(essAddress).then(function(amount){
                    var amount = web3.utils.fromWei(amount.valueOf(),"ether");
                    amount = Number(amount).toFixed(_vue.decimals);
                    _vue.total_bonus = amount;
                });

                //已锁定用于分红提现的奖池
                essContract.methods.lockBonusTotal().call(null,function(err,value){
                    if(!err){
                        var amount = web3.utils.fromWei(value.valueOf(),"ether");
                        amount = Number(amount).toFixed(_vue.decimals);
                        _vue.lock_total_bonus = amount;
                    }
                    else
                    {
                        console.log(err);
                    }
                });
            },
        },
    }
</script>
