                                    border-right: 6px solid transparent;
                                    border-left: 6px solid transparent;
                                }
                            }
                        }
                    }
                }
            }
            .line2 {
                line-height: 2.2rem;
                .line2-list {
                    position: relative;
                    padding-left: 1rem;
                    font-size: 0.8rem;
                    &::before {
                        position: absolute;
                        display: block;
                        content: '';
                        width: 0.6rem;
                        height: 3px;
                        border-radius: 2px;
                        background: #888fa7;
                        top: 1.25rem;
                        left: 0;
                    }
                    span {
                        font-size: 1.5rem;
                        color: #3e4656;
                        height: 2.2rem;
                    }
                }
            }
        }
        .box3{
            margin-top: 1.8rem;
        }

    }
    @media only screen and (max-width: 800px) {
        .tokenbuy{
            .step2{
                width: 100%;
                padding-top: 0;
                padding-bottom: 5rem;
                .step2tit{
                    line-height: 3.6rem;
                    padding: 0 1rem;
                    .left{
                        font-size: 1rem;
                        .strp2tit-num{
                            font-size: 1.4rem;
                        }
                    }
                    .rigth{
                        font-size: 0.8rem;
                        .iconfont{
                            font-size: 0.8rem;
                        }
                    }
                }
                .box1{
                    display: none;
                    padding: 1rem;
                    border-radius: 0;
                    margin-top: 0;
                    margin-bottom: 0.6rem;
                    .num {
                        font-size: 1.7rem;
                    }
                    .time-box {
                        .time-num {
                            font-size: 1.7rem;
                            padding-left: 0.4rem;
                            &::before{
                                font-size: 1.7rem;
                                line-height: 1.7rem;
                                height: 1.7rem;
                                top: 48%;
                            }
                        }
                        .time-list {
                            .time-num{
                                padding-left: 0.4rem;
                            }
                            .unit{
                                padding-left: 0.4rem;
                            }
                        }
                    }
                }
                .box-wap{
                    display: block;
                    .sale{
                        &::after{
                            display: none;
                        }
                    }
                }
                .box2{
                    padding: 1rem;
                    border-radius: 0;
                    margin: 0;
                    .line1{
                        margin: 2.4rem 0.5rem 1.4rem;
                        .progressbar .progressbar-inner .spot-box{
                            width: 1.8rem;
                            height: 1.8rem;
                            .percent{
                                top: -1.9rem;
                                width: 2.8rem;
                                font-size: 0.9rem;
                            }
                        }
                    }
                    .line2{
                        .line2-list{
                            line-height: 1.2rem;
                            padding: 0;
                            &::before{
                                display: none;
                            }
                            span{
                                display: block;
                                line-height: 1.4rem;
                                height: 1.4rem;
                            }
                        }
                    }
                }
                .box3{
                    display: block;
                    margin-top: 2.5rem;
                    padding: 0 1rem;
                    .line1{
                        margin-bottom: 1rem;
                        text-align: center;
                    }
                    .line2{
                        width: 100%;
                    }
                }
            }
        }
    }
</style>





